@mixin all($res){
  @media all and ( max-width: $res )
  {
    @content;
  }
}

@mixin screen($res){
  @media ( max-width: $res )
  {
    @content;
  }
}

@mixin clearfix() {
  &:before,
  &:after {
    content: ""; // 1
    display: table; // 2
  }
  &:after {
    clear: both;
  }
}